@import '../../scss/variable.scss';

.popup-line {
  box-shadow: 0vh 0vh 1vh var(--text-color);
  background: var(--bkg-color);
  border: 1px solid var(--text-color);
  position: absolute;
  z-index: 5;
  p {
    margin: 0;
  }
  ul {
    text-align: left;
    li {
      margin-bottom: 5px;
    }
  }
}
.popup-title {
  border-bottom: 1px solid var(--border-color);
  text-align: left;
  padding: 4px 11px 5px;
  font-size: $ft-s;
  font-weight: bold;
  touch-action: auto;
  cursor: pointer;
  .close-btn {
    position: absolute;
    right: 8px;
    top: 4px;
    font-size: 0.9rem;
    touch-action: auto;
  }
}
.popup-title:hover {
  border-color: var(--text-color);
}
.highlight {
  z-index: 10;
  .popup-title {
    background-color: var(--text-color);
    color: var(--bkg-color) !important;
    .close-btn {
      color: var(--bkg-color) !important;
    }
  }
}

.popup-content {
  text-align: left;
  touch-action: auto;
}

