@media (min-width: 320px) and (max-width: 480px) {
  .popup-line {
    position: relative;
    width: 100% !important;
    max-width: 100% !important;
    margin-bottom: 5px;
    top: auto !important;
    left: auto !important;
    box-shadow: 0vh 0vh 0.5vh var(--text-color) !important;
    transform: initial !important;
  }
}

