@import '../../scss/variable.scss';

#Detail {
  overflow: auto;
}
.detail-layout {
  margin: auto;
  text-align: center;
  .detail-section {
    width: 100%;
    height: 100%;
    left: 0;
    padding: 0 !important;
    overflow: hidden;
    .view-layout {
      width: 100%;
      overflow: auto;
      // padding: 42px 5px 21px; //fs .8
      padding: 45px 5px 21px;
    }
  }
  .container {
    h1 {
      margin: 0;
      font-weight: bold;
      font-size: $ft-s-title;
      letter-spacing: $l-s-bold;
    }
    .desc {
      padding: 0 5px;
    }
    padding: 0 11px 11px;
    max-width: 1200px !important;
  }
}
.detail-video-wrapper {
  position: relative;
  height: 0;
  padding-bottom: 100%;
}
.detail-video {
  max-width: 100%;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
}
.detail-img {
  max-width: 100%;
}
.img2,
.img4 {
  border-left: none;
}
.pd-03 {
  margin-top: 2px;
  padding: 0 3px;
}

