@import './variable.scss';
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700&display=swap');

* {
  -webkit-overflow-scrolling: touch;
}
body,
h1,
h2,
h3,
p,
span,
div {
  transition: all ease 0.2s 0s;
  -moz-transition: all ease 0.2s 0s;
  -webkit-transition: all ease 0.2s 0s;
  font-family: 'Noto Sans KR', sans-serif;
}
h1,
h2,
h3,
p,
span,
div {
  color: var(--text-color) !important;
}

//== LAYOUT ==//
#root,
.App,
#Home {
  // height: 100vh;
}
body {
  background-color: var(--bkg-color);
  min-height: 100vh;
  max-width: 100vw;
  margin: 0;
  // overflow: hidden;
}
.container {
  max-width: 100% !important;
}
.row {
  height: 100%;
}
.col-md-3 {
  margin-bottom: 35px;
  height: fit-content;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 0;
}
.layout {
  width: 100%;
}
.view-layout {
  word-wrap: break-word;
  opacity: 0;
  -webkit-overflow-scrolling: touch;
  transform: scale(0.95);
  transition: all ease 0.4s 0s;
}
.pd-12 {
  padding: 0 12px;
}

//== FONT ==//
p,
a,
h1,
h2,
h3 {
  font-size: $ft-s;
  font-weight: $f-w;
  letter-spacing: $l-s;
}
ul,
div {
  font-weight: $f-w;
  letter-spacing: $l-s;
}
p {
  line-height: $l-h;
  text-align: left;
  margin-bottom: 20px;
}
a {
  color: var(--text-color-blue);
  text-decoration: underline;
  line-height: $l-h;
  transition: all ease 0.1s 0s;
  -moz-transition: all ease 0.1s 0s;
  -webkit-transition: all ease 0.1s 0s;
}
a:hover {
  color: var(--text-color-blue);
  text-decoration: none;
  transition: all ease 0.1s 0s;
  -moz-transition: all ease 0.1s 0s;
  -webkit-transition: all ease 0.1s 0s;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
  li {
    font-size: $ft-s-s;
    a {
      letter-spacing: $l-s;
      font-weight: $f-w;
    }
  }
}
.ft-s-s {
  p,
  a {
    font-size: $ft-s-s;
    line-height: $l-h-s;
  }
}
.ft-s-ss {
  p,
  a {
    font-size: $ft-s-ss;
    line-height: $l-h-ss;
  }
}
.f-n {
  a {
    font-size: $ft-s-f-n;
  }
  position: relative;
  top: -4px;
  -webkit-text-stroke: 0.2px;
  margin-right: 2px;
}
.f-n-s {
  font-size: 0.5rem;
  vertical-align: 6px;
  -webkit-text-stroke: 0.2px;
}
.t-underline {
  text-decoration: underline;
}
.style-list {
  ul {
    padding-left: 15px;
    li:before {
      content: 'Â¶';
      float: left;
      margin-left: -15px;
    }
  }
}

//== LINE ==//
.outLine {
  border: $b-w solid var(--text-color);
  padding: 10px 12px 11px;
  overflow: auto;
  max-height: 73vh;
  margin-top: 50px;
  box-shadow: 0vh 0vh 1vh var(--box-shadow-1);
  background-color: var(--bkg-color-2);
}
.bottomLine {
  display: inline-block;
  margin-bottom: 0;
  font-size: $ft-s;
  font-weight: bold;
  border-bottom: $b-w solid var(--text-color);
  font-family: 'Common-Bold';
  letter-spacing: $l-s-bold;
}

//== DEVICE SCREEN ==//
/*ëë¶ë¶ì ì¤ë§í¸í° ëª¨ë°ì¼ ê¸°ê¸°(ì¸ë¡)*/
@media (min-width: 320px) and (max-width: 480px) {
  .view-layout {
    padding: 45px 11px;
  }
  .outLine {
    margin-top: 33px;
    border: none;
    border-bottom: $b-w solid var(--text-color);
  }
  .col-md-3 {
    padding-left: 8px;
    padding-right: 8px;
  }
}

/*ì í´ìë íë¸ë¦¿, ëª¨ë°ì¼(ê°ë¡)*/
@media (min-width: 481px) and (max-width: 767px) {
  .view-layout {
    padding: 45px 11px;
    -webkit-overflow-scrolling: touch;
  }
  .outLine {
    margin-top: 33px;
    border: none;
    border-bottom: $b-w solid var(--text-color);
  }
  .col-md-3 {
    padding-left: 8px;
    padding-right: 8px;
  }
}
