@import '../../scss/variable.scss';

.about-left {
  margin-top: 50px;
}
.about-mid {
  padding: 50px 10px 40px;
  height: 100vh;
  text-align: center;
}
.about-right {
  margin-top: 50px;
  height: 100vh;
  p {
    font-size: $ft-s-small;
  }
}
.title-center {
  display: block;
  width: fit-content;
  margin: auto;
  margin-bottom: 1.6vh;
}
.about-align-center {
  text-align: center;
  word-break: keep-all;
}
#notice {
  opacity: 0;
  position: absolute;
  background: white;
  border: 1px solid black;
  bottom: 36px;
  text-align: center;
  padding: 3px;
  p {
    margin: 0;
  }
}
.contact-link {
  // display: flex;
  // align-items: center;
}
.icon__tistory {
  width: 10px;
  height: 10px;
  fill: currentColor;
  position: relative;
  top: -1px;
}

