@media (min-width: 320px) and (max-width: 480px) {
  .about-left {
    padding: 0 20px;
  }
  .about-mid {
    padding: 0 20px;
    height: fit-content;
    top: initial;
  }
  .about-right {
    padding: 0 20px;
    padding-bottom: 20px;
    margin-top: 0px;
    height: fit-content;
  }
}

